import axios from 'axios'

const url = 'api/stats'

class StatsService {
// GET TopUsers
    static getTopUsers(limit) {
        return new Promise((resolve, reject) => {
            try {
                if(limit) {
                    axios.get(`${url}/topusers/${limit}`)
                        .then((res) => {
                            const data = res.data
                            resolve(data)
                        })
                } else
                    axios.get(`${url}/topusers`)
                    .then((res) => {
                        const data = res.data
                        resolve(data)
                    })
            } catch(err) {
                reject(err)
            }
        })
    }

// GET TotalTriggers
    static getTotalTriggers() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(`${url}/totalTriggers`)
                .then((res) => {
                    const data = res.data
                    resolve(data)
                })
            } catch(err) {
                reject(err)
            }
        })
    }

    static getTotalServers() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(`${url}/totalServers`)
                .then((res) => {
                    const data = res.data
                    resolve(data)
                })
            } catch(err) {
                reject(err)
            }
        })
    }

    static getTotalUsers() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(`${url}/totalUsers`)
                .then((res) => {
                    const data = res.data
                    resolve(data)
                })
            } catch(err) {
                reject(err)
            }
        })
    }

    static getTopCommands() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(`${url}/topcommands`)
                .then((res) => {
                    const data = res.data
                    resolve(data)
                })
            } catch(err) {
                reject(err)
            }
        })
    }

    static getTopServers() {
        return new Promise((resolve, reject) => {
            try {
                axios.get(`${url}/topservers`)
                .then((res) => {
                    const data = res.data
                    resolve(data)
                })
            } catch(err) {
                reject(err)
            }
        })
    }

}

export default StatsService